import { capitalize, noop } from "lodash";
import React from "react";
import styled from "styled-components";
import { motion } from "framer-motion";

import { Card } from "~/fight/card-piles";
import PaperCard from "../ui/PaperCard";

interface CardPileProps {
  name: "draw" | "discard";
  cards: Card[];
}
const CardPile: React.FC<CardPileProps> = ({ name, cards }) => {
  return (
    <CardPileWrapper>
      <PileTitle>
        {capitalize(name)} Pile: {cards.length}
      </PileTitle>
      <Pile>
        {cards.map((card, i) => {
          return (
            <AnimatedCardWrapper
              key={card.id}
              layoutId={`${card.id}`}
              animate={{
                scale: 0.1,
                rotate: (name === "draw" ? -1 : 1) * 270,
                y: 0,
                // TODO: Setup themes
                backgroundColor: "#ffd700",
              }}
              transition={{ duration: 1 }}
              initial={false}
            >
              <PaperCard card={card} onClick={noop} inTransition />
            </AnimatedCardWrapper>
          );
        })}
      </Pile>
    </CardPileWrapper>
  );
};

const CardPileWrapper = styled.div``;

const Pile = styled.div`
  position: relative;
`;

const PileTitle = styled.div`
  color: #333;
  padding: 10px;
  border-radius: 4px 4px 0 0;
  border-bottom: 2px solid rgba(0, 0, 0, 0.15);
  background: white;
`;

const AnimatedCardWrapper = styled(motion.div)`
  position: absolute;
  bottom: 0;
`;

export default CardPile;
