import Hooks from "~/fight/hooks";
import { ActionTarget } from "~/fight/fight";
import { nextID } from "~/utils/ids";

export const blankSlate = (target: ActionTarget): Hooks.Hook => {
  return {
    id: nextID(),
    p: 1,
    turns: [],
    when: "start",
    effects: [
      {
        name: "CLEAR_CERTAINTY",
        data: { target },
      },
    ],
  };
};
